/* eslint-disable @typescript-eslint/no-explicit-any */

import {Injectable} from '@angular/core';
import {Directionality} from '@angular/cdk/bidi';
import {PlacementArray} from '@bmc-ux/adapt-angular';

@Injectable()
export class AdaptTypeaheadConfig {
  editable: boolean = false;
  focusFirst: boolean = false;
  showHint: boolean = false;
}


/**
 * Configuration service for the AdaptTypeahead component.
 * You can inject this service, typically in your root component, and customize the values of its properties in
 * order to provide default values for all the typeaheads used in the application.
 */
@Injectable()
export class AdaptTypeaheadModel {
  container: any;
  editable: boolean = true;
  focusFirst: boolean = true;
  showHint: boolean = false;
  placement: PlacementArray;
  private _isRtl: boolean = false;

  constructor(private _dir: Directionality) {
    this._isRtl = this._dir.value === 'rtl';
    this.placement = 'auto';
  }
}
